import { ApiResult, ArrayResult, SearchParam } from "./api";
import { Schedule } from "./schedule";
import { Meeting } from "./meeting";
import request from "../utils/request";

export type Profile = {
  loading?: boolean;
  id?: number;
  userId?: number;
  avatarUrl?: string;
  name?: string;
  title?: string;
  subTitle?: string;
  introduction?: string;
  points?: { value: string }[];
  targets?: { value: string }[];
  experiences?: Experience[];
  mainImage?: string;
  images?: string[];
  priceKrw?: number;
  status?: number;
  content?: string;
  schedules?: Array<Schedule>;
};

type Experience = {
  name: string;
  history: { year: string; value: string }[];
};

export async function getProfileList(
  searchParam: SearchParam
): Promise<ApiResult<ArrayResult<Profile>>> {
  return await request.get("/v1/profiles", searchParam);
}

export async function getProfile(
  profileId: number | string
): Promise<ApiResult<Profile>> {
  return await request.get(`/v1/profiles/${profileId}`);
}

export async function createProfile(
  profile: Profile
): Promise<ApiResult<Profile>> {
  return await request.post("/v1/profiles", profile);
}

export async function createMeeting(
  profileId: number,
  meeting: {
    localStartDate: string;
    startTimestamp: number;
    duration: number;
  }
): Promise<ApiResult<Meeting>> {
  return await request.post(`/v1/profiles/${profileId}/meetings`, meeting);
}

export async function getMeetingsByProfileId(
  profileId: number | string
): Promise<ApiResult<Meeting[]>> {
  return await request.get(`/v1/profiles/${profileId}/meetings`);
}
