import { ApiResult } from "./api";
import request from "./../utils/request";

export type User = {
  id: number;
  nickName: string;
  email: string;
  avatarUrl: string;
};

type createUserResType = ApiResult<{
  token: string;
  user: User;
}>;

export async function createUser(
  provider: string,
  data: any
): Promise<createUserResType> {
  const res: createUserResType = await request.post(
    "/v1/users/" + provider,
    data
  );

  //   setCurrentUser(res.result.token, res.result.user);
  return res;
}
