import { useState, useEffect, useRef, createRef } from "react";
import { useHistory } from "react-router-dom";
import { Row, Col, Button, Avatar, Typography } from "antd";
import SimplePeer from "simple-peer";
import { Widget, addResponseMessage } from "react-chat-widget";
import "react-chat-widget/lib/styles.css";
import Icon, {
  VideoCameraOutlined,
  AudioMutedOutlined,
  AudioOutlined,
  StopTwoTone,
} from "@ant-design/icons";
import { ReactComponent as VideoStopSvg } from "./icons/video-stop.svg";
import { ReactComponent as ShareSvg } from "./icons/share.svg";
import Config from "./../../config";
import { User } from "./../../models/user";
import { useCurrentUser } from "./../../state";
import { Meeting } from "./../../models/meeting";
import { useVideo } from "./useVideo";
import { useScreen } from "./useScreen";
import VideoTrack from "./VideoTrack";

interface IMessage {
  event: string;
  from?: {
    userId?: number;
    meetingId?: number;
  };
  data: any;
}

const iceConfiguration = {
  iceServers: [
    {
      urls: "stun:stun.l.google.com:19302",
    },
    // public turn server from https://gist.github.com/sagivo/3a4b2f2c7ac6e1b5267c2f1f59ac6c6b
    // set your own servers here
    {
      url: "turn:192.158.29.39:3478?transport=udp",
      credential: "JZEOEt2V3Qb0y27GRntt2u2PAYA=",
      username: "28224511:1379330808",
    },
  ],
};

interface PeerInfo {
  userId: number;
  peerInstance: SimplePeer.Instance;
  isScreen: boolean;
  muted?: boolean;
  ref: React.RefObject<HTMLVideoElement>;
}

const VideoRoom = ({ meeting }: { meeting: Meeting }) => {
  let history = useHistory();
  const { user } = useCurrentUser();
  const ws = useRef<WebSocket | undefined>();
  const [wsEstablished, setWsEstablished] = useState(false);
  const [remoteVideoPeer, _setRemoteVideoPeer] = useState<PeerInfo>();
  const [remoteScreenPeer, _setRemoteScreenPeer] = useState<PeerInfo>();
  const remoteVideoPeerRef = useRef<PeerInfo | undefined>(remoteVideoPeer);
  const remoteScreenPeerRef = useRef<PeerInfo | undefined>(remoteScreenPeer);
  const setRemoteVideoPeer = (data: PeerInfo | undefined) => {
    remoteVideoPeerRef.current = data;
    _setRemoteVideoPeer(data);
  };
  const setRemoteScreenPeer = (data: PeerInfo | undefined) => {
    remoteScreenPeerRef.current = data;
    _setRemoteScreenPeer(data);
  };

  const localVideoRef = useRef<HTMLVideoElement>(null);
  const {
    stream: screenStream,
    isLive: isScreenOn,
    screenOn,
    screenOff,
  } = useScreen();
  const {
    stream: videoStream,
    videoMuted,
    audioMuted,
    toggleMuteVideo,
    toggleMuteAudio,
  } = useVideo();

  useEffect(() => {
    const onUnload = () => {
      console.log("[unload]Close websocket");
      ws.current?.close();
    };

    window.addEventListener("beforeunload", onUnload);

    return () => {
      onUnload();
      window.removeEventListener("beforeunload", onUnload);
    };
  }, []);

  useEffect(() => {
    if (isScreenOn && screenStream) {
      remoteScreenPeer?.peerInstance.addStream(screenStream);
    } else if (!isScreenOn && screenStream) {
      remoteScreenPeer?.peerInstance.removeStream(screenStream);
    }
  }, [isScreenOn, screenStream]);

  useEffect(() => {
    if (!videoStream) return;

    if (localVideoRef.current) {
      localVideoRef.current.srcObject = videoStream;
    }

    if (!wsEstablished) {
      initWS();
      setWsEstablished(true);
    }
  }, [videoStream]);

  const initWS = () => {
    if (!user) {
      return;
    }
    ws.current = new WebSocket(
      `${Config.WsAddr}/ws?userId=${user.id}&meetingId=${meeting.id}`
    );
    ws.current.onmessage = (e: MessageEvent) => {
      const message: IMessage = JSON.parse(e.data);
      console.log("[Received]", message);
      switch (message.event) {
        case "join":
          if (user.id === message.from!.userId!) {
            break;
          }

          addPeer(message.from!.userId!, false);
          sendMessage({
            event: "peerAdded",
            data: { peerId: message.from!.userId! },
          });
          break;
        case "peerAdded":
          addPeer(message.from!.userId!, true);
          break;
        case "leave":
          removePeer();
          break;
        case "disconnect":
          removePeer();
          history.goBack();
          break;
        case "signal":
          if (message.data.isScreen) {
            remoteScreenPeerRef.current?.peerInstance.signal(
              message.data.signal
            );
          } else {
            remoteVideoPeerRef.current?.peerInstance.signal(
              message.data.signal
            );
          }

          break;
        case "chat":
          addResponseMessage(message.data.message);
          break;
        default:
          console.error("Unrecognized message", message);
      }
    };
  };

  const sendMessage = (message: IMessage) => {
    var jsonMessage = JSON.stringify(message);
    if (ws.current) {
      ws.current.send(jsonMessage);
    }
    console.log("[Send]", message.event, message.data);
  };

  const addPeer = (peerId: number, isInitiator: boolean) => {
    console.log("[addPeer]", peerId, isInitiator);
    const videoPeerInfo: PeerInfo = {
      userId: peerId,
      isScreen: false,
      peerInstance: new SimplePeer({
        initiator: isInitiator,
        stream: videoStream,
        config: iceConfiguration,
      }),
      ref: createRef<HTMLVideoElement>(),
    };

    const screenPeerInfo: PeerInfo = {
      userId: peerId,
      isScreen: true,
      peerInstance: new SimplePeer({
        initiator: isInitiator,
        config: iceConfiguration,
      }),
      muted: true,
      ref: createRef<HTMLVideoElement>(),
    };

    setRemoteVideoPeer(videoPeerInfo);
    setRemoteScreenPeer(screenPeerInfo);

    videoPeerInfo.peerInstance.on("signal", (data: any) => {
      if (data.renegotiate || data.transceiverRequest) return;
      sendMessage({
        event: "signal",
        data: {
          signal: data,
          peerId,
          isScreen: false,
        },
      });
    });

    screenPeerInfo.peerInstance.on("signal", (data: any) => {
      if (data.renegotiate || data.transceiverRequest) return;
      sendMessage({
        event: "signal",
        data: {
          signal: data,
          peerId,
          isScreen: true,
        },
      });
    });

    videoPeerInfo.peerInstance.on("stream", (stream: MediaStream) => {
      if (videoPeerInfo.ref.current)
        videoPeerInfo.ref.current.srcObject = stream;
      stream.getTracks().forEach((track) => {
        track.onmute = (e) => {
          console.log("video onmute");
        };
        track.onunmute = (e) => {
          console.log("video onunmute");
        };
        track.onended = (e) => {
          console.log("video onended");
        };
      });
    });

    remoteScreenPeerRef.current!.peerInstance.on(
      "stream",
      (stream: MediaStream) => {
        if (screenPeerInfo.ref.current) {
          screenPeerInfo.ref.current.srcObject = stream;
        }
        stream.getTracks().forEach((track) => {
          track.onmute = (e) => {
            if (remoteScreenPeerRef.current)
              setRemoteScreenPeer({
                ...remoteScreenPeerRef.current,
                muted: true,
              });
          };
          track.onunmute = (e) => {
            if (remoteScreenPeerRef.current)
              setRemoteScreenPeer({
                ...remoteScreenPeerRef.current,
                muted: false,
              });
          };
          track.onended = (e) => {
            if (remoteScreenPeerRef.current)
              setRemoteScreenPeer({
                ...remoteScreenPeerRef.current,
                muted: true,
              });
          };
        });
      }
    );
  };

  const removePeer = () => {
    if (remoteScreenPeer) remoteScreenPeer.peerInstance.destroy();
    if (remoteVideoPeer) remoteVideoPeer.peerInstance.destroy();
    setRemoteScreenPeer(undefined);
    setRemoteVideoPeer(undefined);
  };

  const sendChatMessage = (message: any) => {
    sendMessage({
      event: "chat",
      data: {
        message,
      },
    });
  };

  const other = (): User => {
    return user?.id === meeting.guest?.id ? meeting.host! : meeting.guest!;
  };

  const onScreenSharing = () => remoteScreenPeer && !remoteScreenPeer.muted;
  const screenColSpan = onScreenSharing() ? { span: 18 } : { span: 0 };
  const videoColSpan = onScreenSharing() ? { span: 6 } : { span: 24 };
  const innerVideoColSpan = onScreenSharing() ? { span: 24 } : { span: 12 };

  return (
    <>
      <Row gutter={8}>
        <Col {...screenColSpan}>
          {remoteScreenPeer && (
            <div
              className="screen"
              style={{ display: remoteScreenPeer.muted ? "none" : "" }}
            >
              <VideoTrack ref={remoteScreenPeer.ref} />
            </div>
          )}
        </Col>
        <Col {...videoColSpan}>
          <Row gutter={8}>
            <Col {...innerVideoColSpan}>
              <div className="face">
                <VideoTrack
                  ref={localVideoRef}
                  videoMuted={videoMuted}
                  audioMuted={audioMuted}
                  nickName={
                    user?.nickName +
                    " (You)" +
                    (isScreenOn ? "  ..." : "")
                  }
                />
              </div>
            </Col>
            <Col {...innerVideoColSpan}>
              {remoteVideoPeer && (
                <div className="face">
                  <VideoTrack
                    ref={remoteVideoPeer.ref}
                    nickName={other().nickName}
                  />
                </div>
              )}
            </Col>
          </Row>
        </Col>
      </Row>
      <div
        style={{
          top: "0",
          position: "fixed",
          width: "100%",
          paddingTop: 6,
          paddingBottom: 3,
          paddingLeft: 30,
          background: "white",
        }}
      >
        <Row>
          <Col span={8}>
            <span style={{ fontSize: 25 }}>{meeting.profileTitle}</span>
          </Col>
          <Col span={8}>
            <div style={{ textAlign: "center" }}>
              <Button
                type="text"
                onClick={toggleMuteAudio}
                icon={audioMuted ? <AudioMutedOutlined /> : <AudioOutlined />}
              >
                 {audioMuted ? "" : ""}
              </Button>
              &nbsp;&nbsp;
              <Button
                type="text"
                onClick={toggleMuteVideo}
                icon={
                  videoMuted ? (
                    <Icon component={VideoStopSvg} />
                  ) : (
                    <VideoCameraOutlined />
                  )
                }
              >
                 {videoMuted ? "" : ""}
              </Button>
              &nbsp;&nbsp;
              <Button
                type="text"
                icon={
                  isScreenOn ? <StopTwoTone /> : <Icon component={ShareSvg} />
                }
                onClick={() => (isScreenOn ? screenOff() : screenOn())}
              >
                 {isScreenOn ? " " : ""}
              </Button>
            </div>
          </Col>
          <Col span={8}>
            <Button
              danger
              type="primary"
              style={{ float: "right", marginRight: "40px", marginTop: "2px" }}
              onClick={() => history.push("/meeting")}
            >
               
            </Button>
          </Col>
        </Row>
      </div>
      <Widget
        handleNewUserMessage={sendChatMessage}
        profileAvatar={other().avatarUrl}
        title={
          <>
            <Avatar src={other().avatarUrl} /> {other().nickName}{" "}
            <Typography.Text type="secondary">
              <small>  </small>
            </Typography.Text>
          </>
        }
        subtitle=""
      />
    </>
  );
};

export default VideoRoom;
