import { useState, useEffect } from "react";
import { Menu, Button, Modal, Avatar, Row, Col, Dropdown } from "antd";
import { Link, useLocation } from "react-router-dom";
import { useHistory } from "react-router-dom";
import { HomeOutlined, VideoCameraOutlined } from "@ant-design/icons";
import { useCurrentUser } from "./../../state";
import RequestHostForm from "./RequestHostForm";
import SocialLogin from "./SocialLogin";

const menus = [
  {
    key: "/",
    icon: <HomeOutlined style={{ width: "100%", fontSize: "180%" }} />,
  },
  {
    key: "/meeting",
    icon: <VideoCameraOutlined style={{ width: "100%", fontSize: "180%" }} />,
  },
];

const Header = () => {
  const [topMenu, setTopMenu] = useState("");
  const [createCourceFormVisible, setCreateCourceFormVisible] = useState(false);
  const [loginModalVisible, setLoginModalVisible] = useState(false);
  const { user, signOut } = useCurrentUser();
  let history = useHistory();
  let location = useLocation();

  useEffect(() => {
    if (location.pathname.startsWith("/meeting")) {
      setTopMenu("/meeting");
    } else {
      setTopMenu("/");
    }
  }, []);

  const logout = () => {
    signOut();
  };

  const loginSuccess = (ok: boolean) => {
    if (ok) {
    }
    setLoginModalVisible(false);
  };

  return (
    <>
      <Row>
        <Col span={22} offset={1}>
          <Menu
            mode="horizontal"
            selectedKeys={[topMenu]}
            onSelect={(m: any) => setTopMenu(m.key)}
            style={{ textAlign: "center" }}
          >
            {menus.map((m) => (
              <Menu.Item key={m.key} style={{ width: 100 }}>
                <Link to={m.key}>{m.icon}</Link>
              </Menu.Item>
            ))}
          </Menu>
        </Col>
        <Col span={1}>
          <div style={{ marginTop: "7px", marginLeft: "-20px" }}>
            {user ? (
              <Dropdown
                overlay={
                  <Menu>
                    <Menu.Item key="0">
                      <Button
                        type="text"
                        onClick={() => setCreateCourceFormVisible(true)}
                      >
                         
                      </Button>
                    </Menu.Item>
                    <Menu.Item key="1">
                      <Button type="text" onClick={() => history.push("/mine")}>
                         
                      </Button>
                    </Menu.Item>
                    <Menu.Item key="2">
                      <a
                        href="/admin/"
                        target="_blank"
                        style={{ marginLeft: "4px" }}
                      >
                        
                      </a>
                    </Menu.Item>
                    <Menu.Divider />
                    <Menu.Item key="3">
                      <Button type="text" onClick={logout}>
                        
                      </Button>
                    </Menu.Item>
                  </Menu>
                }
                trigger={["click"]}
              >
                <Avatar src={user.avatarUrl} />
              </Dropdown>
            ) : (
              <Button
                type="link"
                style={{ marginTop: "2px", marginLeft: "-25px" }}
                onClick={() => setLoginModalVisible(true)}
              >
                
              </Button>
            )}
          </div>
        </Col>
      </Row>
      <RequestHostForm
        visible={createCourceFormVisible && !loginModalVisible}
        onFinish={(ok) => {
          setCreateCourceFormVisible(false);
          if (ok) history.push("/");
        }}
      />
      <Modal
        title="   "
        visible={loginModalVisible}
        onCancel={() => setLoginModalVisible(false)}
        cancelButtonProps={{ style: { display: "none" } }}
        okButtonProps={{ style: { display: "none" } }}
      >
        <SocialLogin onSuccess={loginSuccess} />
      </Modal>
    </>
  );
};
export default Header;
