import { useState } from "react";
import PaypalButtons from "./PaypalButtons";
import { Result, Button, message } from "antd";
import { Order, updatePaypalPayResultToPreOrder } from "../../models/order";

const PaypalPayment = ({ preOrder }: { preOrder: Order }) => {
  const [paidPreOrder, setPaidPreOrder] = useState<Order>();
  const onPaySuccess = async (
    payerId: string,
    orderId: string,
    details: any
  ) => {
    const res = await updatePaypalPayResultToPreOrder(
      preOrder.id,
      payerId,
      orderId,
      details
    );
    if (res.success) {
      message.success(" ");
      setPaidPreOrder(res.data.preOrder);
    } else {
      message.error(res.error?.message);
    }
  };
  if (!paidPreOrder) {
    return <PaypalButtons preOrder={preOrder} onPaySuccess={onPaySuccess} />;
  }
  if (paidPreOrder) {
    return (
      <Result
        status="success"
        title=" ."
        subTitle={
          <>
              : {paidPreOrder.paypalOrderInfo.id} <br />
            {paidPreOrder.paypalOrderInfo.payer.email_address} <br />
            {paidPreOrder.paypalOrderInfo.payer.name.given_name}{" "}
            {paidPreOrder.paypalOrderInfo.payer.name.surname}
          </>
        }
        extra={[
          <Button
            type="primary"
            key="confirm"
            size="large"
            onClick={() => window.location.reload()}
          >
            
          </Button>,
        ]}
      />
    );
  }
  return <></>;
};
export default PaypalPayment;
