import { createContext, useState, useContext, ReactNode } from "react";

export interface VideoInitialValues {
  audioMuted: boolean;
  videoMuted: boolean;
}


interface VideoOptionContextType {
  constraints: MediaStreamConstraints;
  initialValues: VideoInitialValues;
  setConstraints(c: MediaStreamConstraints): void;
  setInitialValues(v: VideoInitialValues): void;
}

export const VideoOptionContext = createContext<VideoOptionContextType>(
  null!
);

export const useVideoOption = () => {
  const context = useContext(VideoOptionContext);
  if (!context) {
    throw new Error(
      "useVideoOption must be used within the VideoOptionProvider"
    );
  }
  return context;
};

const VideoOptionProvider = ({
  children,
}: {
  children: ReactNode;
}) => {
  const [constraints, editConstraints] = useState<MediaStreamConstraints>({
    audio: true,
    video: true,
  });
  const [initialValues, editInitialValues] = useState<VideoInitialValues>({
    audioMuted: false,
    videoMuted: false,
  });

  const setConstraints = (c: MediaStreamConstraints) => editConstraints(c);
  const setInitialValues = (v: VideoInitialValues) => editInitialValues(v);

  return (
    <VideoOptionContext.Provider
      value={{
        constraints,
        initialValues,
        setConstraints,
        setInitialValues,
      }}
    >
      {children}
    </VideoOptionContext.Provider>
  );
};

export default VideoOptionProvider;
