import { useState } from "react";
import { Modal } from "antd";
import { useHistory } from "react-router-dom";
import { Meeting } from "./../../../models/meeting";
import VideoOptionProvider from "../../../components/video/VideoOptionProvider";
import VideoRoom from "./../../../components/video/VideoRoom";
import LocalVideoPreview from "./../../../components/video/LocalVideoPreview";

const VideoContainer = ({ meeting }: { meeting: Meeting }) => {
  const [previewVisible, setPreviewVisible] = useState<boolean>(true);
  let history = useHistory();

  return (
    <VideoOptionProvider>
      {!previewVisible && <VideoRoom meeting={meeting} />}
      <Modal
        visible={previewVisible}
        width={300}
        onOk={() => setPreviewVisible(false)}
        onCancel={() => history.push("/meeting")}
        okText=""
        cancelText=""
        closable={false}
      >
        <LocalVideoPreview />
      </Modal>
    </VideoOptionProvider>
  );
};

export default VideoContainer;
