import React from "react";
import { List, Avatar, Tag } from "antd";
import { Profile } from "../../../models/profile";
import { weekdayStr } from "../../../utils/time_util";

const ProfileInfoView: React.FC<{ profile: Profile }> = ({ profile }) => {
  return (
    <>
      <List.Item.Meta
        avatar={
          <Avatar size={64} src={profile.avatarUrl} />
        }
        description={profile.schedules?.map((schedule: any) => (
          <p key={schedule.id}>
            <Tag>{weekdayStr(schedule.localWeekday!)}</Tag>
            {`${schedule.localStartTime} ~ ${schedule.localEndTime}`}
          </p>
        ))}
      />
    </>
  );
};

export default ProfileInfoView;
