import React, { useState } from "react";
import { Steps, Button, Modal, Form, Input, Checkbox, Select } from "antd";
import { MinusCircleOutlined, PlusOutlined } from "@ant-design/icons";
import dayjs from "dayjs";
import "./RequestHostForm.less";
import { Profile, createProfile } from "../../models/profile";

const { Step } = Steps;

const layout = {
  labelCol: { span: 5 },
  wrapperCol: { span: 16 },
};

const formItemLayoutWithOutLabel = {
  wrapperCol: {
    xs: { span: 24, offset: 0 },
    sm: { span: 16, offset: 5 },
  },
};

export type RequestHostFormProps = {
  onFinish: (ok?: boolean) => void;
  visible: boolean;
};

const RequestHostForm: React.FC<RequestHostFormProps> = (props) => {
  const [current, setCurrent] = useState(0);
  const [title, setTitle] = useState("");
  const [images, setImages] = useState<Array<string>>([]);
  const [content, setContent] = useState("");
  const [weekdays, setWeekdays] = useState<Array<number>>([]);
  const [startTime, setStartTime] = useState<any>();
  const [duration, setDuration] = useState<any>();
  const [price, setPrice] = useState<any>();

  const availableTimes = [
    { hour: 0, minute: 0 },
    { hour: 0, minute: 30 },
    { hour: 1, minute: 0 },
    { hour: 1, minute: 30 },
    { hour: 2, minute: 0 },
    { hour: 2, minute: 30 },
    { hour: 3, minute: 0 },
    { hour: 3, minute: 30 },
    { hour: 4, minute: 0 },
    { hour: 4, minute: 30 },
    { hour: 5, minute: 0 },
    { hour: 5, minute: 30 },
    { hour: 6, minute: 0 },
    { hour: 6, minute: 30 },
    { hour: 7, minute: 0 },
    { hour: 7, minute: 30 },
    { hour: 8, minute: 0 },
    { hour: 8, minute: 30 },
    { hour: 9, minute: 0 },
    { hour: 9, minute: 30 },
    { hour: 10, minute: 0 },
    { hour: 10, minute: 30 },
    { hour: 11, minute: 0 },
    { hour: 11, minute: 30 },
    { hour: 12, minute: 0 },
    { hour: 12, minute: 30 },
    { hour: 13, minute: 0 },
    { hour: 13, minute: 30 },
    { hour: 14, minute: 0 },
    { hour: 14, minute: 30 },
    { hour: 15, minute: 0 },
    { hour: 15, minute: 30 },
    { hour: 16, minute: 0 },
    { hour: 16, minute: 30 },
    { hour: 17, minute: 0 },
    { hour: 17, minute: 30 },
    { hour: 18, minute: 0 },
    { hour: 18, minute: 30 },
    { hour: 19, minute: 0 },
    { hour: 19, minute: 30 },
    { hour: 20, minute: 0 },
    { hour: 20, minute: 30 },
    { hour: 21, minute: 0 },
    { hour: 21, minute: 30 },
    { hour: 22, minute: 0 },
    { hour: 22, minute: 30 },
    { hour: 23, minute: 0 },
  ];
  const steps = [
    {
      title: "  ",
      content: (
        <Form {...layout}>
          <Form.Item label="">
            <Input value={title} onChange={(e) => setTitle(e.target.value)} />
          </Form.Item>
          <Form.Item label="">
            <Input.TextArea
              value={content}
              onChange={(e) => setContent(e.target.value)}
            />
          </Form.Item>
          <Form.List name="names" initialValue={[{}]}>
            {(fields, { add, remove }) => (
              <>
                {fields.map((field, index) => (
                  <Form.Item
                    {...(index === 0 ? layout : formItemLayoutWithOutLabel)}
                    label={index === 0 ? "" : ""}
                    key={field.key}
                  >
                    <Form.Item noStyle>
                      <Input
                        style={{ width: "89%" }}
                        onChange={(e) => {
                          console.log(field);
                          const list = [...images];
                          list.splice(index, 1, e.target.value);
                          setImages(list);
                        }}
                      />
                    </Form.Item>
                    {fields.length > 1 ? (
                      <MinusCircleOutlined
                        className="dynamic-delete-button"
                        onClick={() => {
                          const list = [...images];
                          list.splice(index, 1);
                          setImages(list);
                          remove(field.name);
                        }}
                      />
                    ) : null}
                  </Form.Item>
                ))}
                <Form.Item {...formItemLayoutWithOutLabel}>
                  <Button
                    type="dashed"
                    onClick={() => {
                      const list = images;
                      list.push("");
                      setImages(list);
                      add();
                    }}
                    style={{ width: "90%" }}
                    icon={<PlusOutlined />}
                  >
                    Add
                  </Button>
                </Form.Item>
              </>
            )}
          </Form.List>
        </Form>
      ),
    },
    {
      title: "",
      content: (
        <Form {...layout}>
          <Form.Item label=" ">
            <Checkbox.Group onChange={(v: Array<any>) => setWeekdays(v)}>
              <Checkbox value={0}> </Checkbox>
              <Checkbox value={1}> </Checkbox>
              <Checkbox value={2}> </Checkbox>
              <Checkbox value={3}> </Checkbox>
              <Checkbox value={4}> </Checkbox>
              <Checkbox value={5}> </Checkbox>
              <Checkbox value={6}> </Checkbox>
            </Checkbox.Group>
          </Form.Item>
          <Form.Item label=" ">
            <Select
              style={{ width: 90 }}
              onSelect={(v: number) => setStartTime(v)}
            >
              {availableTimes.map((t) => (
                <Select.Option
                  key={`${t.hour}:${t.minute}:00`}
                  value={`${t.hour}:${t.minute}:00`}
                >
                  {`${("0" + t.hour).slice(-2)}:${("0" + t.minute).slice(-2)}`}
                </Select.Option>
              ))}
            </Select>{" "}
            {" "}
            <Select
              style={{ width: 90 }}
              onSelect={(v: number) => setDuration(v)}
            >
              <Select.Option value={1}>1</Select.Option>
              <Select.Option value={2}>2</Select.Option>
              <Select.Option value={3}>3</Select.Option>
              <Select.Option value={4}>4</Select.Option>
              <Select.Option value={5}>5</Select.Option>
              <Select.Option value={6}>6</Select.Option>
              <Select.Option value={7}>7</Select.Option>
              <Select.Option value={8}>8</Select.Option>
              <Select.Option value={9}>9</Select.Option>
              <Select.Option value={10}>10</Select.Option>
            </Select>
          </Form.Item>
        </Form>
      ),
    },
    {
      title: "",
      content: (
        <Form {...layout}>
          <Form.Item label="">
            <Input
              value={price}
              style={{ width: 100 }}
              onChange={(e) => setPrice(e.target.value)}
            />{" "}
             / 1
          </Form.Item>
        </Form>
      ),
    },
  ];

  const onDone = async () => {
    const profile: Profile = {
      title: title,
      content: content,
      images: images,
      priceKrw: parseInt(price),
      schedules: weekdays.map((weekday: any) => {
        const startAt = dayjs(
          "2006-01-01 " + startTime,
          "YYYY-MM-DD H:m:ss"
        ).add(parseInt(weekday), "day");
        const utcStartAt = startAt.subtract(startAt.utcOffset(), "minute");
        const utcBase = utcStartAt
          .set("hour", 0)
          .set("minute", 0)
          .set("second", 0)
          .set("milliseconds", 0);

        return {
          weekday: utcStartAt.day(),
          startTimestamp: utcStartAt.unix() - utcBase.unix(),
          duration: parseInt(duration) * 60 * 60,
        };
      }),
    };

    const res = await createProfile(profile);
    if (!res.success) {
      return;
    }
    props.onFinish(true);
  };

  const next = () => {
    setCurrent(current + 1);
  };

  const prev = () => {
    setCurrent(current - 1);
  };

  const stepsAction = () => {
    return (
      <>
        {current > 0 && (
          <Button style={{ margin: "0 8px" }} onClick={() => prev()}>
            
          </Button>
        )}
        {current < steps.length - 1 && (
          <Button type="primary" onClick={() => next()}>
            
          </Button>
        )}
        {current === steps.length - 1 && (
          <Button type="primary" onClick={onDone}>
            
          </Button>
        )}
      </>
    );
  };
  return (
    <Modal
      width={640}
      bodyStyle={{ padding: "32px 40px 48px" }}
      destroyOnClose
      title={" "}
      visible={props.visible}
      footer={stepsAction()}
      onCancel={() => props.onFinish(false)}
    >
      <Steps current={current}>
        {steps.map((item) => (
          <Step key={item.title} title={item.title} />
        ))}
      </Steps>
      <br />
      {steps[current].content}
    </Modal>
  );
};

export default RequestHostForm;
