import { useEffect, useState } from "react";
import { Layout, List, Button, Avatar, message } from "antd";
import { CheckCircleOutlined, CheckCircleFilled } from "@ant-design/icons";
import {
  OrderPrepare,
  Order,
  getOrderPrepare,
  createPreOrder,
} from "../../models/order";
import { Meeting } from "./../../models/meeting";
import { useCurrentUser } from "./../../state";
import PaypalPayment from "./PaypalPayment";

type CartItem = {
  checked: boolean;
  meeting: Meeting;
};
const PaymentIndex: React.FC = () => {
  const [orderPrepare, setOrderPrepare] = useState<OrderPrepare>();
  const [cartItems, setCartItems] = useState<CartItem[]>();
  const [checkedAmount, setCheckedAmount] = useState(0);
  const [checkedQuantity, setCheckedQuantity] = useState(0);
  const [preOrder, setPreOrder] = useState<Order>();
  const { user } = useCurrentUser();

  useEffect(() => {
    if (!user) return;
    (async () => {
      const res = await getOrderPrepare();
      if (res.success) {
        setOrderPrepare(res.data);
        if (res.data.meetings) {
          setCartItems(
            res.data.meetings.map((m) => ({ checked: false, meeting: m }))
          );
        }
      }
    })();
  }, [user]);

  const addToOrder = (meeting: Meeting) => {
    setCheckedAmount(checkedAmount + (meeting.priceKrw || 0));
    setCheckedQuantity(checkedQuantity + 1);
    setCartItems(
      cartItems?.map((t) =>
        t.meeting.id !== meeting.id
          ? t
          : {
              checked: true,
              meeting: meeting,
            }
      )
    );
  };
  const removeFromOrder = (meeting: Meeting) => {
    setCheckedAmount(checkedAmount - (meeting.priceKrw || 0));
    setCheckedQuantity(checkedQuantity - 1);
    setCartItems(
      cartItems?.map((t) =>
        t.meeting.id !== meeting.id
          ? t
          : {
              checked: false,
              meeting: meeting,
            }
      )
    );
  };

  const handleCreatePreOrder = async () => {
    if (!cartItems) return;
    const res = await createPreOrder(
      cartItems.filter((t) => t.checked).map((t) => t.meeting.id!)
    );
    if (res.success) {
      setPreOrder(res.data);
    } else {
      message.error(res.error?.message);
    }
  };

  if (
    !orderPrepare ||
    !orderPrepare.meetings ||
    orderPrepare.meetings.length === 0
  ) {
    return <></>;
  }
  return (
    <Layout.Content className="center">
      <List
        header={
          <div style={{ height: "32px" }}>
            <span style={{ fontSize: 20, marginRight: 10 }}></span>
             {orderPrepare.meetings.length}
          </div>
        }
        bordered
        dataSource={cartItems}
        footer={
          <div style={{ height: 40 }}>
            <span
              style={{ fontSize: 25, color: "#F08080", fontWeight: "bold" }}
            >
              {checkedAmount}
            </span>
            <Button
              type="primary"
              size="large"
              style={{ float: "right" }}
              disabled={checkedQuantity <= 0 || preOrder !== undefined}
              onClick={handleCreatePreOrder}
            >
               {checkedQuantity} 
            </Button>
          </div>
        }
        renderItem={(cartItem) => (
          <List.Item>
            <List.Item.Meta
              avatar={
                <Button
                  type="text"
                  disabled={preOrder !== undefined}
                  icon={
                    cartItem.checked ? (
                      <CheckCircleFilled
                        style={{ fontSize: "30px", color: "#F08080" }}
                      />
                    ) : (
                      <CheckCircleOutlined
                        style={{ fontSize: "30px", color: "Gainsboro" }}
                      />
                    )
                  }
                  onClick={() =>
                    cartItem.checked
                      ? removeFromOrder(cartItem.meeting)
                      : addToOrder(cartItem.meeting)
                  }
                />
              }
              title={
                <>
                  <Avatar
                    size="small"
                    src={cartItem.meeting.guest?.avatarUrl}
                    style={{ marginRight: 5 }}
                  />
                  {cartItem.meeting.host?.nickName ||
                    cartItem.meeting.host?.email}
                </>
              }
              description={cartItem.meeting.profileTitle}
              style={{ width: "50%" }}
            />
            <span className="price">{cartItem.meeting.priceKrw}</span>
          </List.Item>
        )}
        style={{ marginBottom: "12px" }}
      />
      {preOrder && <PaypalPayment preOrder={preOrder} />}
    </Layout.Content>
  );
};
export default PaymentIndex;
