import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import {
  Layout,
  Card,
  Image,
  Typography,
  Row,
  Col,
  Badge,
  List,
  Tag,
} from "antd";
import dayjs from "dayjs";
import {
  Profile,
  getProfile,
  getMeetingsByProfileId,
} from "../../models/profile";
import ProfileInfoView from "./components/ProfileInfoView";
import MeetingRequestForm from "./components/MeetingRequestForm";
import { durationToString, weekdayStr } from "../../utils/time_util";
import { Meeting } from "../../models/meeting";

const ProfileDetail = () => {
  const [profile, setProfile] = useState<Profile>({});
  const [meetings, setMeetings] = useState<Meeting[]>([]);
  const { id } = useParams<{ id: string }>();

  useEffect(() => {
    (async () => {
      const res = await getProfile(id);
      if (res.success) {
        setProfile(res.data);
      }
    })();

    (async () => {
      const res = await getMeetingsByProfileId(id);
      if (res.success) {
        setMeetings(res.data);
      }
    })();
  }, []);

  const upcomingMeetings =
    meetings &&
    Object.entries(
      meetings
        ?.filter((m) => dayjs.unix(m.startAt!) >= dayjs())
        .reduce((obj: any, m) => {
          const t = dayjs.unix(m.startAt!);
          const k = t.format("YYYY MM DD ") + weekdayStr(t.day());
          if (obj[k] === undefined) obj[k] = [];
          obj[k].push({
            time: t.format("HH:mm"),
            duration: durationToString(m.duration!),
          });
          return obj;
        }, {})
    ).map((m) => {
      return {
        date: m[0],
        times: m[1] as {
          time: string;
          duration: string;
        }[],
      };
    });

  return (
    <div>
      <div
        style={{
          position: "relative",
          backgroundImage: `url(${profile.mainImage})`,
          height: "100vh",
          backgroundSize: "cover",
        }}
      >
        <div
          style={{
            position: "absolute",
            top: "10%",
            left: "5%",
            zIndex: 1,
          }}
        >
          <Typography.Title level={3}>{profile.title}</Typography.Title>
          <Typography.Title level={4} type="secondary">
            {profile.subTitle}
          </Typography.Title>
          <Typography.Title level={2} style={{ color: "#F08080" }}>
            {profile.name}
          </Typography.Title>
        </div>
      </div>
      <Layout.Content className="center" style={{ marginTop: "-151px" }}>
        <br />
        <br />
        <Row gutter={12}>
          <Col sm={24} md={15}>
            <Card style={{ marginBottom: 12 }}>
              <div
                style={{ maxWidth: "640px" }}
                dangerouslySetInnerHTML={{ __html: profile.introduction! }}
              ></div>
            </Card>
            <Card style={{ marginBottom: 12 }}>
              <Image.PreviewGroup>
                {profile.images?.map((v) => (
                  <Image
                    key={v}
                    width={285}
                    src={v}
                    style={{ paddingRight: "3px", paddingLeft: "3px" }}
                  />
                ))}
              </Image.PreviewGroup>
            </Card>
            <Card title="   " style={{ marginBottom: 12 }}>
              <ul>
                {profile.points &&
                  profile.points.map((v, i) => <li key={i}>{v.value}</li>)}
              </ul>
            </Card>
            <Card
              title="   "
              style={{ marginBottom: 12 }}
            >
              <ul>
                {profile.targets &&
                  profile.targets.map((v, i) => <li key={i}>{v.value}</li>)}
              </ul>
            </Card>
            <Card title="" style={{ marginBottom: 12 }}>
              <ul>
                {profile.experiences &&
                  profile.experiences.map((v) => (
                    <li key={v.name}>
                      <dl>
                        <dt>{v.name}</dt>
                        {v.history.map((h, i) => (
                          <dd key={i}>
                            <Tag>{h.year}</Tag>
                            {h.value}
                          </dd>
                        ))}
                      </dl>
                    </li>
                  ))}
              </ul>
            </Card>
          </Col>
          <Col sm={24} md={8}>
            <Card
              title={<ProfileInfoView profile={profile} />}
              style={{ marginBottom: 12 }}
            >
              <Typography.Title level={5}> </Typography.Title>
              <MeetingRequestForm
                profile={profile}
                meetings={meetings}
                onSuccess={async (ok: boolean) => {
                  if (!ok) return;

                  const res = await getMeetingsByProfileId(id);
                  if (res.success) {
                    setMeetings(res.data);
                  }
                }}
              />
            </Card>
            <Card title=" " style={{ marginBottom: 12 }}>
              {upcomingMeetings && upcomingMeetings.length
                ? upcomingMeetings.map((m) => (
                    <List
                      key={m.date}
                      style={{ width: "160px" }}
                      header={m.date}
                      dataSource={m.times}
                      renderItem={(item) => (
                        <List.Item>
                          <Badge color={"blue"} text={item.time} />
                          &nbsp;&nbsp;&nbsp;
                          {item.duration}
                        </List.Item>
                      )}
                    />
                  ))
                : ""}
            </Card>
          </Col>
        </Row>
      </Layout.Content>
    </div>
  );
};

export default ProfileDetail;
