import { ApiResult, ArrayResult, SearchParam } from "./api";
import { User } from "./user";
import request from "./../utils/request";

export type Meeting = {
  loading?: boolean;
  id?: number;
  startAt?: number;
  duration?: number;
  profileTitle?: string;
  priceKrw?: number;
  host?: User;
  guest?: User;
};

export async function getMeetingList(
  searchParam: SearchParam
): Promise<ApiResult<ArrayResult<Meeting>>> {
  return await request.get("/v1/meetings", searchParam);
}

export async function getMeeting(
  meetingId: number | string
): Promise<ApiResult<Meeting>> {
  return await request.get(`/v1/meetings/${meetingId}`);
}
