import React, { useState, useEffect } from "react";
import { Link, useLocation, useHistory } from "react-router-dom";
import { Layout, Row, Col, Card, Typography, Space, Button } from "antd";
import { MessageOutlined, LikeOutlined } from "@ant-design/icons";
import { Profile, getProfileList } from "../../models/profile";
import ProfileInfoView from "./components/ProfileInfoView";

const IconText = (props: any) => (
  <Space>
    {React.createElement(props.icon)}
    {props.text}
  </Space>
);

const ProfileList = () => {
  const [profiles, setProfiles] = useState<Array<Profile>>([]);
  const [hasMore, setHasMore] = useState(false);
  const [skipCount, setSkipCount] = useState(0);
  const [initLoading, setInitLoading] = useState(true);
  const [loading, setLoading] = useState(false);
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    search();
  }, []);

  const search = async () => {
    setLoading(true);

    const q = new URLSearchParams(location.search).get("q") || "";
    const maxResultCount = 10;
    const res = await getProfileList({
      limit: maxResultCount,
      offset: skipCount,
    });
    setInitLoading(false);
    setLoading(false);
    if (!res || !res.success) {
      return;
    }

    if (res.data && res.data.items) {
      setProfiles(profiles.concat(res.data.items));
      setHasMore(res.data.total > skipCount + maxResultCount);
    }
    setSkipCount(skipCount + maxResultCount);
  };

  const onLoadMore = () => {
    setProfiles(
      profiles.concat(
        [...new Array<Profile>(3)].map(
          (i): Profile => ({
            loading: true,
          })
        )
      )
    );
    search();
    window.dispatchEvent(new Event("resize"));
  };
  return (
    <Layout.Content className="center">
      <Row gutter={16}>
        {profiles.map((p) => (
          <Col key={p.id} sm={24} md={12}>
            <Card
              onClick={() => history.push(`/profile/${p.id}`)}
              hoverable
              style={{ maxWidth: 480, marginBottom: 16 }}
              cover={<img alt="main" src={p.mainImage} />}
              actions={[
                <IconText icon={LikeOutlined} text={10} key="likes" />,
                <IconText icon={MessageOutlined} text={20} key="comments" />,
                <Button key="go">
                  <Link to={`/profile/${p.id}`}> </Link>
                </Button>,
              ]}
            >
              <ProfileInfoView profile={p} />

              <Typography.Title level={5} style={{ color: "#F08080" }}>
                {p.name}
              </Typography.Title>
              <Card.Meta title={p.title} description={p.subTitle} />
            </Card>
          </Col>
        ))}
      </Row>
    </Layout.Content>
  );
};

export default ProfileList;
