import { createContext, useState, useEffect } from "react";
import { useVideoOption } from "./VideoOptionProvider";

interface IVideoContext {
  stream: MediaStream;
}

export const VideoContext = createContext<IVideoContext>(null!);

export const useVideo = () => {
  const [stream, setStream] = useState<MediaStream>();
  const { constraints, initialValues, setInitialValues } = useVideoOption();
  const [videoMuted, setVideoMuted] = useState(initialValues.videoMuted);
  const [audioMuted, setAudioMuted] = useState(initialValues.audioMuted);

  useEffect(() => {
    (async () => {
      const s = await navigator.mediaDevices.getUserMedia(constraints);
      if (audioMuted) {
        setAudioMuted(true);
        s.getAudioTracks()[0].enabled = false;
      }
      if (videoMuted) {
        setVideoMuted(true);
        s.getVideoTracks()[0].enabled = false;
      }
      setStream(s);
    })();
  }, []);

  const toggleMuteVideo = () => {
    if (!stream) return;

    setInitialValues({ audioMuted: audioMuted, videoMuted: !videoMuted });
    setVideoMuted(!videoMuted);
    const track = getVideoTrack();
    if (track) track.enabled = videoMuted;
  };

  const toggleMuteAudio = () => {
    if (!stream) return;

    setInitialValues({ audioMuted: !audioMuted, videoMuted: videoMuted });
    setAudioMuted(!audioMuted);
    const track = getAudioTrack();
    if (track) track.enabled = audioMuted;
  };

  const getAudioTrack = () => stream?.getAudioTracks()[0];
  const getVideoTrack = () => stream?.getVideoTracks()[0];

  return {
    stream: stream,
    videoMuted,
    audioMuted,
    toggleMuteVideo,
    toggleMuteAudio,
  };
};
