import { useEffect, useRef } from "react";
import { Button } from "antd";
import Icon, {
  VideoCameraOutlined,
  AudioMutedOutlined,
  AudioOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { ReactComponent as VideoStopSvg } from "./icons/video-stop.svg";
import { useVideo } from "./useVideo";

const videoSize = { width: "250px", height: "187px" };
const LocalVideoPreview = () => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const {
    stream: videoStream,
    videoMuted,
    audioMuted,
    toggleMuteVideo,
    toggleMuteAudio,
  } = useVideo();

  useEffect(() => {
    if (videoRef.current && videoStream)
      videoRef.current.srcObject = videoStream;
  }, [videoStream]);

  return (
    <>
      <div
        style={{
          width: videoSize.width,
          height: videoSize.height,
          position: "relative",
        }}
      >
        {videoMuted && (
          <UserOutlined
            style={{
              fontSize: "50px",
              color: "white",
              position: "absolute",
              lineHeight: videoSize.height,
              textAlign: "center",
              // margin: "auto",
              top: 0,
              bottom: 0,
              left: 0,
              right: 0,
            }}
          />
        )}
        <video
          ref={videoRef}
          autoPlay
          muted
          width={videoSize.width}
          height={videoSize.height}
        />
      </div>
      <br />
      <Button
        key={"audio"}
        size="large"
        onClick={toggleMuteAudio}
        icon={audioMuted ? <AudioMutedOutlined /> : <AudioOutlined />}
      >
         {audioMuted ? "" : ""}
      </Button>
      <Button
        key={"video"}
        onClick={toggleMuteVideo}
        size="large"
        icon={
          videoMuted ? (
            <Icon component={VideoStopSvg} />
          ) : (
            <VideoCameraOutlined />
          )
        }
        style={{ float: "right" }}
      >
         {videoMuted ? "" : ""}
      </Button>
    </>
  );
};
export default LocalVideoPreview;
