import React, { useState, useEffect } from "react";
import dayjs from "dayjs";
import { useHistory } from "react-router-dom";
import {
  Layout,
  List,
  Tag,
  Button,
  Skeleton,
  Avatar,
  Modal,
  message,
} from "antd";
import { PlusOutlined } from "@ant-design/icons";
import useWindowSize from "./../../hooks/useWindowSize";
import { Meeting, getMeetingList } from "./../../models/meeting";
import { useCurrentUser } from "./../../state";
import SocialLogin from "./../../components/layout/SocialLogin";
import { weekdayStr } from "./../../utils/time_util";
import Payment from "./../../components/payment";

const MeetingView: React.FC<{ meeting: Meeting }> = ({ meeting }) => {
  const startAt = dayjs.unix(meeting.startAt!);
  const endAt = dayjs.unix(meeting.startAt!).add(meeting.duration!, "seconds");

  return (
    <>
      <p>{meeting.profileTitle}</p>
      <p>
        {startAt.format("YYYY-MM-DD")} <Tag>{weekdayStr(startAt.day())}</Tag>{" "}
        {`${startAt.format("HH:mm")} ~ ${endAt.format("HH:mm")}`}
      </p>
      <p>
        <Tag color="magenta"></Tag>{" "}
        {meeting.host ? (
          <>
            <Avatar size={"small"} src={meeting.host.avatarUrl} />{" "}
            {meeting.host.nickName}
          </>
        ) : null}
      </p>
      <p>
        <Tag color="cyan"></Tag>{" "}
        {meeting.guest ? (
          <>
            <Avatar size={"small"} src={meeting.guest.avatarUrl} />{" "}
            {meeting.guest.nickName}
          </>
        ) : null}
      </p>
    </>
  );
};

const MeetingIndex = () => {
  const [meetings, setMeetings] = useState<Array<Meeting>>([]);
  const [hasMore, setHasMore] = useState(false);
  const [loading, setLoading] = useState(false);
  const [skipCount, setSkipCount] = useState(0);
  const [initLoading, setInitLoading] = useState(true);
  const [loginModalVisible, setLoginModalVisible] = useState(false);
  const { width, height } = useWindowSize();
  const { user } = useCurrentUser();
  let history = useHistory();

  useEffect(() => {
    if (user) loadMeetings();
  }, [user]);

  const loadMeetings = async () => {
    setLoading(true);
    const maxResultCount = 10;
    const res = await getMeetingList({
      userId: user?.id,
      limit: maxResultCount,
      offset: skipCount,
    });
    if (!res.success) {
      setLoading(false);
      return;
    }

    if (res.data && res.data.items) {
      setMeetings(meetings.concat(res.data.items));
      setHasMore(res.data.total > skipCount + maxResultCount);
    }
    setInitLoading(false);
    setLoading(false);
    setSkipCount(skipCount + maxResultCount);
  };
  const onLoadMore = () => {
    setMeetings(
      meetings.concat(
        [...new Array<Meeting>(3)].map(
          (i): Meeting => ({
            loading: true,
          })
        )
      )
    );
    loadMeetings();
    window.dispatchEvent(new Event("resize"));
  };

  const socialLoginSuccessCallback = (ok: boolean) => {
    setLoginModalVisible(false);
    if (ok) {
      window.location.reload();
    }
  };

  const socialLoginCancel = () => {
    setLoginModalVisible(false);
  };

  const canEnter = (meeting: Meeting): boolean => {
    if (!user) return false;
    return meeting.host?.id === user.id || meeting.guest?.id === user.id;
  };

  const goMeetingPage = (meeting: Meeting) => {
    if (!user) {
      setLoginModalVisible(true);
      return;
    }
    if (!canEnter(meeting)) {
      message.warn("  ");
      return;
    }
    history.push(`/meeting/${meeting.id}`);
  };

  return (
    <Layout.Content className="center">
      {width > 700 && (
        <div
          style={{
            width: "400px",
            right: 0,
            position: "fixed",
            marginTop: "10px",
            marginRight: "10px",
            zIndex: 999,
          }}
        >
          {user && (
            <div style={{ height: height - 50, overflow: "auto" }}>
              <Payment />
            </div>
          )}
        </div>
      )}
      <List
        loading={initLoading}
        itemLayout="vertical"
        split={false}
        loadMore={
          !initLoading && !loading && hasMore ? (
            <div
              style={{
                textAlign: "center",
                marginTop: 12,
                height: 32,
                lineHeight: "32px",
              }}
            >
              <Button
                type="dashed"
                style={{ width: "90%" }}
                icon={<PlusOutlined />}
                onClick={onLoadMore}
              >
                
              </Button>
            </div>
          ) : null
        }
        size="large"
        dataSource={meetings}
        footer={<div>{/* <b>ant design</b> footer part */}</div>}
        renderItem={(meeting) => {
          return (
            <List.Item
              key={meeting.id}
              actions={[
                <Button
                  onClick={() => goMeetingPage(meeting)}
                  disabled={!canEnter(meeting)}
                  key="go"
                >
                  
                </Button>,
              ]}
            >
              <Skeleton avatar title={false} loading={meeting.loading} active>
                <MeetingView meeting={meeting} />
              </Skeleton>
            </List.Item>
          );
        }}
      />
      {width <= 700 && user && <Payment />}
      <Modal
        title="   "
        visible={loginModalVisible}
        onCancel={socialLoginCancel}
        cancelButtonProps={{ style: { display: "none" } }}
        okButtonProps={{ style: { display: "none" } }}
      >
        <SocialLogin onSuccess={socialLoginSuccessCallback} />
      </Modal>
    </Layout.Content>
  );
};
export default MeetingIndex;
