import { extend } from "umi-request";
import Config from "./../config";

const apiURL = (path: string) => Config.ApiAddr + path;
const tz = Intl.DateTimeFormat().resolvedOptions().timeZone;
const _request = extend({
  headers: {
    "Content-Type": "application/json",
    "X-Timezone": tz,
  },
});

const request = {
  get: (path: string, params: any = null) =>
    _request(apiURL(path), { method: "GET", params }),
  post: (path: string, body: any) =>
    _request(apiURL(path), { method: "POST", data: body }),
  put: (path: string, body: any) =>
    _request(apiURL(path), { method: "PUT", data: body }),
  delete: (path: string, body: any) =>
    _request(apiURL(path), { method: "DELETE", data: body }),
};

export default request;
