import { ApiResult, ArrayResult, SearchParam } from "./api";
import { Meeting } from "./meeting";
import request from "../utils/request";

export type Order = {
  id: number;
  description: string;
  totalAmountKrw: number;
  payAmountKrw: number;
  method: string;
  note: string;
  status: string;
  meetingIds: number[];
  paypalOrderInfo: any;
  chargedAt: Date;
  createdAt: Date;
  updatedAt: Date;
};

export type OrderPrepare = {
  totalAmountKrw: number;
  meetings: Meeting[];
};

export async function getOrderList(
  searchParam: SearchParam
): Promise<ApiResult<ArrayResult<Order>>> {
  return await request.get("/v1/orders", searchParam);
}

export async function getOrderPrepare(): Promise<ApiResult<OrderPrepare>> {
  return await request.get("/v1/orders/prepare");
}

export async function getOrder(
  orderId: number | string
): Promise<ApiResult<Order>> {
  return await request.get(`/v1/orders/${orderId}`);
}

export async function createOrder(
  meetingIds: number[]
): Promise<ApiResult<Order>> {
  return await request.post(`/v1/orders`, { meetingIds });
}

export async function createPreOrder(
  meetingIds: number[]
): Promise<ApiResult<Order>> {
  return await request.post(`/v1/orders/pre`, { meetingIds });
}

export async function updatePaypalPayResultToPreOrder(
  preOrderId: number,
  payerId: string,
  orderId: string,
  orderInfo: any
): Promise<ApiResult<{ preOrder: Order; order: Order }>> {
  return await request.put(`/v1/orders/pre/` + preOrderId, {
    payerId,
    orderId,
    orderInfo,
  });
}
