import { forwardRef } from "react";
import { Space } from "antd";
import Icon, {
  VideoCameraOutlined,
  AudioMutedOutlined,
  AudioOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { ReactComponent as VideoStopSvg } from "./icons/video-stop.svg";
import AvatarIcon from "./icons/AvatarIcon";

interface IVideoTrack {
  id?: string;
  videoMuted?: boolean;
  audioMuted?: boolean;
  nickName?: string;
}

const VideoTrack = forwardRef<HTMLVideoElement, IVideoTrack>(
  ({ videoMuted, audioMuted, nickName }, videoRef) => {
    return (
      <div
        style={{
          position: "relative",
          textAlign: "center",
        }}
      >
        <Space
          style={{
            position: "absolute",
            color: "white",
            background: "grey",
            zIndex: 2,
            paddingLeft: 5,
            paddingRight: 5,
          }}
        >
          {audioMuted ? <AudioMutedOutlined /> : <AudioOutlined />}
          {videoMuted ? (
            <Icon component={VideoStopSvg} />
          ) : (
            <VideoCameraOutlined />
          )}
          {nickName}
        </Space>
        <video width='100%' ref={videoRef} autoPlay muted />
      </div>
    );
  }
);

export default VideoTrack;
