import {
  createContext,
  useCallback,
  useState,
  useEffect,
  useContext,
  ReactNode,
} from "react";
import request, { RequestOptionsInit } from "umi-request";
import { User, createUser } from "./../models/user";

interface CurrentUserContextType {
  user?: User;
  token?: string;
  signIn(provider: string, data: any): Promise<void>;
  signOut(): Promise<void>;
}

export const CurrentUserContext = createContext<CurrentUserContextType>(null!);

export const useCurrentUser = () => {
  const context = useContext(CurrentUserContext);
  if (!context) {
    throw new Error(
      "useCurrentUser must be used within the CurrentUserProvider"
    );
  }
  return context;
};

const CurrentUserProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | undefined>();
  const [token, setToken] = useState<string | undefined>();

  useEffect(() => {
    const token = window.localStorage.getItem("token");
    if (token) {
      setToken(token);
    }
    const userJSON = window.localStorage.getItem("user");
    if (userJSON) {
      setUser(JSON.parse(userJSON));
    }
  }, []);

  const signIn = useCallback((provider: string, data: any) => {
    return createUser(provider, data).then((res) => {
      if (!res.success) {
        throw Error(res.error?.message);
      }
      window.localStorage.setItem("user", JSON.stringify(res.data.user));
      window.localStorage.setItem("token", res.data.token);
      setUser(res.data.user);
      setToken(res.data.token);
    });
  }, []);

  const signOut = useCallback(() => {
    setUser(undefined);
    setToken(undefined);
    window.localStorage.removeItem("user");
    window.localStorage.removeItem("token");
    return Promise.resolve();
  }, []);

  request.interceptors.request.use(
    (url: string, options: RequestOptionsInit) => {
      return {
        url: url,
        options: {
          ...options,
          headers: {
            ...options.headers,
            Authorization: `Bearer ${token}`,
          },
        },
      };
    }
  );

  return (
    <CurrentUserContext.Provider value={{ user, token, signIn, signOut }}>
      {children}
    </CurrentUserContext.Provider>
  );
};

export default CurrentUserProvider;
