import { createContext, useState } from "react";

interface IVideoContext {
  stream: MediaStream;
}

export const ScreenContext = createContext<IVideoContext>(null!);

export const useScreen = () => {
  const [stream, setStream] = useState<MediaStream>();
  const [isLive, setIsLive] = useState<boolean>(false);

  const screenOn = async () => {
    if (isLive) return;
    setIsLive(true);

    const mediaDevice = navigator.mediaDevices as any;
    const s = (await mediaDevice.getDisplayMedia({
      cursor: true,
    })) as MediaStream;
    s.getVideoTracks()[0].onended = () => {
      screenOff();
    };

    setStream(s);

    return;
  };
  const screenOff = () => {
    setIsLive(false);
    stream?.getTracks().map((track) => track.stop());
    setStream(undefined);
  };

  return {
    stream,
    isLive,
    screenOn,
    screenOff,
  };
};
