import { useState, useEffect } from "react";
import {
  Typography,
  Form,
  Alert,
  Select,
  Button,
  Modal,
  Tag,
  message,
} from "antd";
import {
  DoubleLeftOutlined,
  LeftOutlined,
  RightOutlined,
  DoubleRightOutlined,
} from "@ant-design/icons";
import Calendar from "./../../../components/Calendar";
import dayjs from "dayjs";
import { useCurrentUser } from "../../../state";
import { Profile, createMeeting } from "../../../models/profile";
import { Meeting } from "../../../models/meeting";
import { TimePeriod } from "../../../models/schedule";
import { durationToString, weekdayStr } from "../../../utils/time_util";
import SocialLogin from "../../../components/layout/SocialLogin";

type AvailableTimePeriod = {
  start: TimePeriod;
  end: TimePeriod;
};
const MeetingTimeForm = ({
  localDate,
  availables,
  onStartTimeChanged,
  onDurationChanged,
}: {
  localDate: string;
  availables: AvailableTimePeriod[];
  onStartTimeChanged: (v: number) => void;
  onDurationChanged: (v: number) => void;
}) => {
  const [startTimePeriod, setStartTimePeriod] = useState<TimePeriod>();
  const [endTimePeriod, setEndTimePeriod] = useState<TimePeriod>();
  const [duration, setDuration] = useState<number>(0);

  useEffect(() => {
    setStartTimePeriod(undefined);
    setEndTimePeriod(undefined);
    setDuration(0);
  }, [localDate]);

  useEffect(() => {
    if (!startTimePeriod) return;

    let startTimestamp = dayjs(
      `${localDate} ${startTimePeriod.localTime}`,
      "YYYY-MM-DD HH:mm"
    );

    if (startTimePeriod.isNextDay) {
      startTimestamp = startTimestamp.add(1, "day");
    }

    if (duration > 0) {
      const currentEndTime = startTimePeriod.timestamp + duration;
      const lastTimePeriod = availables[availables.length - 1].end;
      if (currentEndTime <= lastTimePeriod.timestamp) {
        setEndTimePeriod(
          availables.find((p) => p.end.timestamp === currentEndTime)?.end
        );
      } else {
        setEndTimePeriod(lastTimePeriod);
        setDuration(lastTimePeriod.timestamp - startTimePeriod.timestamp);
      }
    }

    if (onStartTimeChanged) onStartTimeChanged(startTimestamp.unix());
  }, [startTimePeriod]);

  useEffect(() => {
    if (!endTimePeriod) return;
    if (!startTimePeriod) return;

    const d = endTimePeriod.timestamp - startTimePeriod.timestamp;
    if (duration !== d) {
      setDuration(d);
    }
  }, [endTimePeriod]);

  useEffect(() => {
    if (onDurationChanged) onDurationChanged(duration);
  }, [duration]);

  if (!availables || availables.length === 0) {
    return <></>;
  }

  return (
    <>
      <Form.Item
        label={
          <>
            <span style={{ marginRight: "65px" }}> </span>
            {duration > 0 && (
              <Tag color="volcano">{durationToString(duration)} </Tag>
            )}
          </>
        }
      >
        <Select
          style={{ width: 90 }}
          value={startTimePeriod?.timestamp}
          onSelect={(v: number) =>
            setStartTimePeriod(
              availables.find((p) => p.start.timestamp === v)?.start
            )
          }
        >
          {availables.map((p) => (
            <Select.Option key={p.start.timestamp} value={p.start.timestamp}>
              {p.start.localTime}
            </Select.Option>
          ))}
        </Select>
        &nbsp;  &nbsp;
        <Select
          style={{ width: 90 }}
          value={endTimePeriod?.timestamp}
          onSelect={(v: number) =>
            setEndTimePeriod(availables.find((p) => p.end.timestamp === v)?.end)
          }
        >
          {availables.map((p) => (
            <Select.Option key={p.end.timestamp} value={p.end.timestamp}>
              {p.end.localTime}
            </Select.Option>
          ))}
        </Select>
        &nbsp;
      </Form.Item>
    </>
  );
};

const MeetingRequestForm: React.FC<{
  profile: Profile;
  meetings: Meeting[];
  onSuccess: (ok: boolean) => void;
}> = ({ profile, meetings, onSuccess }) => {
  const [startDate, setStartDate] = useState<dayjs.Dayjs>();
  const [startTimestamp, setStartTimestamp] = useState<number>(0);
  const [duration, setDuration] = useState<number>(0);
  const [errorMessage, setErrorMessage] = useState<string>();
  const [loginModalVisible, setLoginModalVisible] = useState(false);
  const { user } = useCurrentUser();

  useEffect(() => {
    setStartTimestamp(0);
    setDuration(0);
  }, [startDate]);

  useEffect(() => {
    if (startDate && startTimestamp > 0 && duration > 0 && meetings) {
      for (let i = 0; i < meetings.length; i++) {
        if (isConflictTime(meetings[i])) {
          setErrorMessage("  .   ");
          return;
        }
      }
    }
    setErrorMessage(undefined);
  }, [startDate, startTimestamp, duration]);

  if (!profile) return <></>;

  const isConflictTime = (meeting: Meeting) => {
    const existStartTime = meeting.startAt!;
    const existEndTime = existStartTime + meeting.duration!;

    if (
      existStartTime >= startTimestamp &&
      existStartTime < startTimestamp + duration
    ) {
      console.log(meeting, startTimestamp, duration);
      return true;
    }

    if (startTimestamp >= existStartTime && startTimestamp < existEndTime) {
      console.log(meeting, startTimestamp, duration);
      return true;
    }

    if (
      startTimestamp + duration > existStartTime &&
      startTimestamp + duration <= existEndTime
    ) {
      console.log(meeting, startTimestamp, duration);
      return true;
    }
    return false;
  };

  const clearState = () => {
    setStartDate(undefined);
    setStartTimestamp(0);
    setDuration(0);
  };

  const createMeetingSubmit = async (v: any) => {
    if (!user) {
      setLoginModalVisible(true);
      return false;
    }

    if (!startDate || !startTimestamp || !duration) {
      message.error("    ");
      return false;
    }

    const hide = message.loading("");
    try {
      const res = await createMeeting(profile.id!, {
        localStartDate: startDate.format("YYYY-MM-DD"),
        startTimestamp: startTimestamp,
        duration: duration,
      });

      if (!res) {
        return false;
      }

      if (res && res.success) {
        message.success("");
        onSuccess(true);
        clearState();
        return true;
      }
    } finally {
      hide();
    }
    return false;
  };

  const socialLoginSuccessCallback = (ok: boolean) => {
    setLoginModalVisible(false);
    if (ok) {
      createMeetingSubmit(null);
      window.location.reload();
    }
  };

  const socialLoginCancel = () => {
    setLoginModalVisible(false);
  };

  const disabledDate = (current: dayjs.Dayjs): boolean => {
    if (current < dayjs().endOf("day")) {
      return true;
    }

    if (!profile.schedules) {
      return false;
    }

    return (
      profile.schedules?.findIndex(
        (schedule) => schedule.localWeekday === current.day()
      ) < 0
    );
  };

  const availableLocalTimePeriods = (): {
    start: TimePeriod;
    end: TimePeriod;
  }[] => {
    if (!startDate) return [];
    if (!profile || !profile.schedules) return [];

    const periods = profile.schedules.find(
      (s) => s.localWeekday === startDate.day()
    )?.availablTimePeriods;

    if (!periods) return [];

    const output: {
      start: TimePeriod;
      end: TimePeriod;
    }[] = [];
    for (let i = 0; i < periods.length - 1; i++) {
      output.push({
        start: periods[i],
        end: periods[i + 1],
      });
    }
    return output;
  };

  const calendarHeaderRender = ({
    value,
    onChange,
  }: {
    value: dayjs.Dayjs;
    onChange: (date: dayjs.Dayjs) => void;
  }) => {
    const current = value.clone();
    return (
      <div style={{ width: "100%", textAlign: "center" }}>
        <Button
          type="text"
          size="small"
          icon={<DoubleLeftOutlined />}
          style={{ float: "left" }}
          onClick={() => {
            onChange(current.subtract(1, "year"));
          }}
        />
        <Button
          type="text"
          size="small"
          icon={<LeftOutlined />}
          style={{ float: "left" }}
          onClick={() => {
            onChange(current.subtract(1, "month"));
          }}
        />

        <Typography.Title style={{ display: "inline-block" }} level={5}>
          {value.format("YYYY MM")}
        </Typography.Title>

        <Button
          type="text"
          size="small"
          icon={<DoubleRightOutlined />}
          style={{ float: "right" }}
          onClick={() => {
            onChange(current.add(1, "year"));
          }}
        />
        <Button
          type="text"
          size="small"
          icon={<RightOutlined />}
          style={{ float: "right" }}
          onClick={() => {
            onChange(current.add(1, "month"));
          }}
        />
      </div>
    );
  };

  return (
    <>
      {profile.schedules && (
        <Calendar
          fullscreen={false}
          disabledDate={disabledDate}
          onSelect={(m) => {
            if (disabledDate(m)) {
              setStartDate(undefined);
              return;
            }
            setStartDate(m);
          }}
          headerRender={calendarHeaderRender}
        />
      )}
      <br />
      {startDate ? (
        <Alert
          message={`${startDate?.format("YYYY-MM-DD")} ${weekdayStr(
            startDate?.day()
          )}`}
          type="success"
          showIcon
        />
      ) : (
        <Alert message="  " type="info" showIcon />
      )}
      <br />
      {startDate && (
        <Form layout="vertical" onFinish={createMeetingSubmit}>
          <MeetingTimeForm
            localDate={startDate.format("YYYY-MM-DD")}
            availables={availableLocalTimePeriods()}
            onStartTimeChanged={(v) => setStartTimestamp(v)}
            onDurationChanged={(v) => setDuration(v)}
          />
          <Form.Item colon={false}>
            <Button
              type="primary"
              htmlType="submit"
              // size="large"
              style={{ width: "100%" }}
              disabled={
                errorMessage !== undefined ||
                startTimestamp === 0 ||
                duration <= 0
              }
            >
              
            </Button>
          </Form.Item>
        </Form>
      )}
      {errorMessage && <Alert message={errorMessage} type="error" />}
      <Modal
        title="   "
        visible={loginModalVisible}
        onCancel={socialLoginCancel}
        cancelButtonProps={{ style: { display: "none" } }}
        okButtonProps={{ style: { display: "none" } }}
      >
        <SocialLogin onSuccess={socialLoginSuccessCallback} />
      </Modal>
    </>
  );
};

export default MeetingRequestForm;
