import { Suspense } from "react";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import { Layout } from "antd";
import CurrentUserProvider from "./state";
import Header from "./components/layout/Header";
import ProfileIndex from "./pages/profile";
import ProfileDetail from "./pages/profile/detail";
import MeetingIndex from "./pages/meeting";
import MeetingDetail from "./pages/meeting/detail";
import MineIndex from "./pages/mine";

import "./App.less";

const App = () => (
  <Layout style={{ background: "white" }}>
    <CurrentUserProvider>
      <Router>
        <header>
          <Header />
        </header>
        <div className="body">
          <Suspense fallback={null}>
            <Switch>
              <Route exact path="/" component={ProfileIndex} />
              <Route exact path="/profile/:id" component={ProfileDetail} />
              <Route exact path="/meeting" component={MeetingIndex} />
              <Route exact path="/meeting/:id" component={MeetingDetail} />
              <Route exact path="/mine" component={MineIndex} />
            </Switch>
          </Suspense>
        </div>
      </Router>
    </CurrentUserProvider>
  </Layout>
);

export default App;
